* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: #111;
  background: #e3e2e2;
  overflow-x: hidden;
  font-family: "Quicksand", sans-serif;
}

img {
  width: 100%;
  object-fit: cover;
}

p,
li {
  line-height: 22px;
  letter-spacing: 1px;
  margin: 0.7rem 0;
  font-size: 1rem !important;
  font-family: "Quicksand", sans-serif;
  /* font-family: "Quicksand", sans-serif; */
  /* font-weight: 500; */
}

a,
a:hover {
  text-decoration: none;
  cursor: pointer;
  border: none !important;
}

ul {
  list-style: none !important;
}

/* ::-webkit-scrollbar {
  width: 7px;
  background: #e3e2e2;
} */

::-webkit-scrollbar-thumb {
  background: rgb(44, 44, 44);
  border-radius: 5px;
}

.loader {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background: linear-gradient(135deg, #50423e 0%, #555 100%);
  color: #f6871e;
}

.loader p {
  color: #fff;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
}

.loader h1 {
  text-transform: uppercase;
  font-size: 3.5rem;
  letter-spacing: 2px;
  font-family: Georgia, "Times New Roman", Times, serif;
}
